@import url('https://fonts.googleapis.com/css2?family=EB+Garamond&family=Moon+Dance&family=Pacifico&family=Satisfy&display=swap');@import url('https://fonts.googleapis.com/css2?family=Moon+Dance&family=Pacifico&family=Satisfy&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap');
:root {
  --contrast: #F99417;
  --mainColor: white;
  --textColor: #191A19;
}
html{
  scroll-behavior: smooth;
}
.complete{
  background-color: transparent;
  width: 100% !important;
}
.App {
  text-align: center;
  width: 100% !important;
  background-color: transparent;
  position: relative;
  scroll-behavior: smooth;
}

.sideDiv{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-width: 100%;
  background-color: var(--mainColor);
  border-bottom: 1px solid rgb(189, 189, 189);
}
.middle{
  z-index: 10;
  display: block;
  flex-wrap: wrap;
  height: auto;
  width: auto;
  text-align: left;
  padding: 50px 20px 0 30px;
  border-radius: 50%;
  max-width: 600px;
  height: fit-content;
}
.middle h2{
    font-weight: 600;
    color: var(--textColor);
}
.middle > p{
  font-size: 30px;
  color: var(--contrast);

}
.hello{
  width: 25px;
  transform-origin: right bottom;
  transform: rotateZ(30deg);
  animation: shake 2s infinite ease-in-out;
  margin-right: 10px;
}
@keyframes shake {
  0%{transform: rotateZ(30deg);}
  5%{transform: rotateZ(60deg);}
  10%{transform: rotateZ(30deg);}
  15%{transform: rotateZ(60deg);}
  20%{transform: rotateZ(30deg);}
}

.typewriter{
  font-size: calc(25px + 2vw);
  color: var(--textColor);
}
.belowMid{
  margin-top: 50px;
  display: flex;
  align-self: auto;
  margin-bottom: 100px;
  align-items: center;
  justify-content: center;
}
.belowMid a{
  
  text-decoration: none;
  border: 2.5px solid var(--contrast) ;
  border-radius: 5px;
  padding: 8px 12px;
  color: var(--contrast);
  transition: 0.5s;
  font-size: 30px;
  font-weight: 500;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.belowMid a:hover{
  background-color: var(--contrast);
  color: var(--mainColor);
  border-color: var(--mainColor);
}
.clipart{
  z-index: 10;
  display: flex;
}

.clipart > img{
  padding:0 0 20px 20px;
  width: 600px;
  max-width: 90%;
}

@media (max-height:800px){
    .social{
      display: none !important;
    }
    
  }
@media (max-width:600px){
  
    .myemail{
      display: none !important;
    }
    .profile{
      max-width: 70vw !important;
    }
    
    form input,textarea{
      width: 70% !important;
    }
    .content{
      font-size: 18px !important;
    }
    .project{
      max-height: 450px;
    }
    
  }

  @import url('https://fonts.googleapis.com/css2?family=Anton&family=Inconsolata&display=swap');@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
#about {
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: var(--textColor);
  min-height: 100vh;
  display: flex;
  border-bottom: 1px solid rgb(189, 189, 189);
  padding: 50px 0;
}
.aboutTitle{
    font-family: 'Courier New', Courier, monospace;
    color: var(--mainColor);
}
.maincontent{
    color: var(--textColor);
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    display: flex;
    width: 100vw;
}

.content{

    position: relative;
    height: auto;
    width: auto;
    text-align: left;
    margin-left: 5%;
    max-width: 80%;
    width: 800px;
    margin-right: 5%;
    font-size: 20px;
    border-radius: 50%;
    
}
.content ul{
  z-index: 11;
    list-style: none;
    color: var(--mainColor);
    font-family: 'Source Sans Pro', sans-serif;
    padding: 0;
}

.skillList > li{
  z-index: 10;
  display: flex;
  flex-wrap: wrap;
  
}
.skillList > li > .item1{
  display: flex;
}
.skillList > li > .item1 > img{
  width: 50px !important;
  height: 50px !important;
  padding: 20px 20px 0 0;
}

.side{
  z-index: 10;
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 50px;
}

.profile{
  z-index: 10;
    max-width: 30vw;
    outline: 2px solid var(--mainColor);
    outline-offset: 15px;
    border-radius: 5px;
    z-index: 10;
}

.content p{
    color: var(--mainColor);
}
.content h1{
    margin-bottom: 50px;
}

@media (max-width:1000px) {
  .projectCard{
    flex-wrap: wrap !important;
    padding-bottom: 20px;
  }
  .projectContent, .projectImage{
    width: 90% !important;
  }
  .projectContent{
    width: 100% !important;
  }

  .projectInfo{
    width: 100% !important;
  }
  .projectButtons{
    margin-top: 30px !important;
    margin-bottom: 20px;
  }
  .projectButtons > a{
    padding: 10px 30px !important;
  }

  #about{
    width: fit-content;
  }
    .maincontent{
      display: inline-block;
    }
    .side{
      margin-top: 50px;
    }
    .profile{
      max-width: 55vw ;
    }
    
}

.progress {
  display: block;
  width: 100%;
  z-index: 10;
  background: rgb(135, 135, 135);
  justify-content: flex-start;
  border-radius: 100px !important;
  align-items: center;
  position: relative;
  padding: 0 5px;
  display: flex;
  height: 21px !important;
  max-width: 500px;
  margin-top: 5px;
  color: var(--textColor);
  min-width: 250px;
}
.progress-value {
  z-index: 10;
  border-radius: 100px;
  height: 15px;
  font-size: 13px;
  font-weight: 600;
  padding-left: 10px;
  margin-right: 20px;
  
}
#one{
  width: 80%;
  background-color: rgb(253, 94, 36);
}

#two{
  width: 80%;
  background-color: rgb(50, 94, 190);
}

#three{
  width: 70%;
  background-color: rgb(200, 200, 31);
}

#four{
  width: 70%;
  background-color: rgb(58, 203, 251);
}

#five{
  width: 80%;
  background-color: rgb(199, 46, 3);
}

.contact{
  text-align: center;
  align-items: center;
  width: 100%;
  max-width: 100vw;
  background-color: var(--textColor);
  min-height: 100%;
  display: flex;
  min-height: 100vh;
  justify-content: space-around;
  overflow: hidden;
  flex-wrap: wrap;
}

#content{
  text-align: left;
  color: var(--mainColor);
  min-width: 300px;
  float: left;
  align-content: center;
  margin: 50px 0;
  z-index: 10;
  border-radius: 50%;
  padding: 50px;
}
#content h1{
  font-size: xxx-large;
  margin-top: 0 ;
}

.contact-link{
  color: var(--mainColor);
  text-decoration: none;
}

.contact-link:hover{
  color: var(--contrast);
}

.contact p{
  font-size: 18px;
}
.contact img{
  width: 25px;
  margin-bottom: -5px;
  margin-right: 10px;
}
.mydetails{
  margin-top: 20px;
}
.formdiv{
  width: fit-content;
  min-width: 400px;
  border-radius: 10px;
  max-width: 90vw;
  overflow: hidden;
  margin: 50px 0;
  z-index: 50;
}
form input,textarea{
  color: var(--textColor);
  margin: 10px;
  padding-bottom: 10px;
  border: none;
  border-bottom: 2px solid var(--mainColor);
  width: 400px;
  font-size: 15px;
  background-color: transparent !important;
  
}

form input:focus{
  outline: none;
  border-bottom: 2px solid var(--contrast);
}

form h2{
  color: var(--mainColor);
  align-self: flex-start;
}
#submitbutton{
  padding: 7px 12px;
  height: 40px;
  border: 0;
  background: var(--contrast);
  font-size: 14px;
  margin: auto;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 20px;
}
#message{
  height: 80px;
  vertical-align: bottom ;
}

.projects{
  padding-bottom: 5vw;
  text-align: center;
  width: 100%;
  background-color: var(--mainColor);
  min-height: 100vh;
  border-bottom: 1px solid rgb(189, 189, 189);
  
}

.projects > h1{
  align-self: center;
  margin-top: 0px;
  padding: 80px 0 0 0;
  color: var(--textColor);
  font-family: 'Courier New', Courier, monospace;
  font-size: calc(25px + 1vw);
}

.projectCard{
  width: 70%;
  margin: 20px ;
  overflow: hidden;
  display: flex;
  justify-content: center;
  box-shadow: 3px 3px 10px grey;
}
.projectImage{
  width: 50%;
  margin: 2%;
  transition: all 0.3s ease-in;
}
.projectImage > img{
  width: 70%;
  border-radius: 10px;

}
.projectInfo{
  padding-top: 15px;
  width: 50%;
  height: 100%;
  justify-content: center;

}

.projectContent{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.projectCard:hover .projectImage{
  transform: scale(1.05);
}
.projectButtons{
  margin-top: 20%;
  margin-bottom: 20px;
  width: 100%;
}
.projectButtons > a{
  padding: 10px 40px;
  border-radius: 50px;
  border: none;
  background-color: var(--textColor);
  color: var(--mainColor);
  cursor: pointer;
  font-weight: 700;
  font-size: calc(10px + 0.5vw);
  text-decoration: none;
  margin: 20px;
}
.projectButtons > a:hover{
  background-color: rgb(55, 55, 55);
}
.projectInfo p{
  color: grey;
}